<template>
  <div>
  
 <div class="container-fluid " style="" >
    <div class="row">
        <div class="col-sm-12 " style="height:1300px;background:rgba(225, 233, 236, 0.911);padding-top:30px;">
          <div class="row">
              <div style=" margin: auto;margin-top:10vh;padding:0;
              background:#FEFEFE;
                " class="rounded-lg shadow-lg border-2  col-10 col-sm-4">
            <div class="bg-blue " style="width:100%;height:10px;">
            </div>
            <br>
            <div class="text-right">
              <img src="@/static/btn.png" class="kinoLightBox img-fluid  z-depth-1 mx-auto" style="height:70px;padding-right:10px;">
            </div>
                <div class="text-center">
              <img src="@/static/linebtn.png" class="kinoLightBox img-fluid  z-depth-1 mx-auto" style="height:10px;">
              <br>
              <div class="container-fluid">
                  <div class="row">
                  
                     <!-- <div class="col-4">
                       <img src="@/static/driver/logo_bkp.png" class="kinoLightBox img-fluid  z-depth-1 mx-auto float-left" style="height:60px;">
                       </div>
                     <div class="col-4">
                       <img src="@/static/driver/sdd.png" class="kinoLightBox img-fluid  z-depth-1 mx-auto" style="height:60px;">
                       </div>
                     <div class="col-4">
                       <img src="@/static/driver/pkss.png" class="kinoLightBox img-fluid  z-depth-1 mx-auto float-right" style="height:60px;">
                       </div> -->
                      </div>
              </div>
                </div>
                  <form action="#" @submit.prevent="login" class="bg-blue pt-2">
                    <div class="sm-form">
                      <label style="margin-left:100px;" class="text-white font-bold font-times" for="username">Username</label>
                      <input
                        type="text"
                         style="width:60%;margin-left:100px;"
                        id="username"
                        name="username"
                        class="form-control form-control-sm "
                       
                        v-model="vdata.username"
                        required
                      />
                    </div>
                    <div class="sm-form">
                      <label style="margin-left:100px;" class="text-white font-bold font-times" for="password">Password</label>
                      <input
                        type="password"
                         style="width:60%;margin-left:100px;"
                        id="password"
                        name="password"
                        class="form-control form-control-sm "
                        v-model="vdata.password"
                        required
                      />
                    </div>
                    <br />
                    <hr class="style13" />
                    <div class="text-center">
                    <button type="submit" class="btn btn-sm bg-blue-darker text-white "><span class="typcn typcn-key"></span> Login</button>
                    </div>
                    <!-- <button type="button" class="btn btn-sm bg-blue-darker text-white float-right" @click="$router.push('/driver/register')">Register</button> -->
                  
                    <br />
                  </form>
              </div>
          </div>

            <br>
            <br>
        </div>
        <div class="col-sm-6">

        </div>
    </div>
</div>
<!-- <div style="" class="text-center font-bold font-times">Created By Malik</div> -->
<!-- <div style="" class="text-center font-bold font-times">TAM PRODUCTIONS</div> -->
  </div>
</template>
<script>
import axios from 'axios'
import md5 from 'md5'
var deferredPrompt;
export default {
  data () {
    return {
      vdata:{
        content:"content",
        title:"title",
        url: {
        link : "http://localhost:8080",
      },
        id_penerima:"1",
        timestamp: Date.now() + 32400000
      },
      ready:true,
    }
  },
  beforeMount () {
        let fd2 = new FormData()
          fd2.append('data', 'select * from user')
          axios
            .post('http://localhost:8080' + '/api/mysql/ld/getUser.php', fd2, {
              headers: {
                Authorization: localStorage.getItem('auth._token.local')
              }
            })
            .then(res => {
              console.log(res)
              this.$store.commit('changeUsers', res.data[0])
              localStorage.setItem('users', JSON.stringify(res.data[0]))
              console.log(res.data[0])
              this.$router.push('/ld/')
            })
  },
  methods: {
    login () {
      console.log(this.vdata.username , this.vdata.password,  md5(this.vdata.password))
      if(this.vdata.username == 'admin' && this.vdata.password == 'btnkantorcabangbanjarbaru'){
        this.$router.push('/driver/admin')
      }else{
      let fd = new FormData()
      fd.append('username', this.vdata.username)
      fd.append('password', md5(this.vdata.password))
      axios
        .post('http://localhost:8080/api/mysql/ld/login.php', fd)
        .then(res => {
          // alert('sukses')
          console.log(res)
          localStorage.setItem('auth._token.local', res.data.token)
          localStorage.setItem('auth.local', res.data.token)
          let fd2 = new FormData()
          fd2.append('data', 'select * from user')
          axios
            .post('http://localhost:8080' + '/api/mysql/ld/getUser.php', fd2, {
              headers: {
                Authorization: localStorage.getItem('auth._token.local')
              }
            })
            .then(res => {
              console.log(res)
              this.$store.commit('changeUsers', res.data[0])
              localStorage.setItem('users', JSON.stringify(res.data[0]))
              console.log(res.data[0])
              this.$router.push('/ld/')
            })
        })
        .catch(err => {
          alert('username salah')
        })
      }
    },
     kirim() {
      let fd = new FormData()
      fd.append('data',JSON.stringify(this.vdata))
      fd.append('database','loandocument')
      axios
        .post("http://localhost:8080/api/send_push_notification.php", fd)
        .then(res => {
          console.log(res);
        });
    },
    install() {
      console.log(deferredPrompt)
      if (deferredPrompt !== undefined) {
        deferredPrompt.prompt();
        deferredPrompt.userChoice.then(function(choiceResult) {
          if (choiceResult.outcome == "dismissed") {
            console.log("User cancelled home screen install");
          } else {
            console.log("User added to home screen");
          }
          deferredPrompt = null;
        });
      }
    },
     subscribe() {
        if ('Notification' in window) {
          Notification.requestPermission(result => {
            // cek apakah user menerima notifiksinya
            if (result !== 'granted') {
              console.log('notifikasi tidak di perbolehkan')
            } else {
              this.confPushSub()
              console.log('notifikasi aktif')
            }
          })
        } else {
          alert('notifikasi tidak didukung ')
        }
      },
      confPushSub() {
        let that = this
        if (!('serviceWorker' in navigator)) {
          // cek apakah service worker ada/tidak
          return
        }
        navigator.serviceWorker.ready.then(function(sw) {
          console.log(sw)
          sw.pushManager.getSubscription().then(sub => {
            // cek apakah device ini sudah subscribe atau belum
            if (sub === null) {
              sw.pushManager
                .subscribe({
                  userVisibleOnly: true,
                  applicationServerKey: that.urlB64ToUint8Array('BHb6hCsHUmbFb8xXLP46iv_xzns76xPniKia5FiBC1sd7RiAiMuRnv1MbGo-ey5oKYwb_UIo9PBWB-yIrlHJ184')
                })
                .then(subscription => {
                  console.log('Subscribe OK:', subscription)
                  that.subsss = subscription
                  let fd = new FormData()
                  fd.append('data', `insert into subs (subs) values ('${JSON.stringify(subscription.toJSON())}')`)//database setting di server
                  fd.append('database','loandocument')
                  axios.post('http://localhost:8080/api/mysql/auto.php', fd
                  , {
                      headers: {
                      Authorization: localStorage.getItem('auth._token.local')
                      }
                  }).then(res => {
                      console.log(res)
                      this.$forceUpdate()
                  })
                })
                .then((res) => {
                  console.log(res);
                  that.aktif = false
                  localStorage.setItem('subscribe', true)
                  that.newNotif('Berhasil Subscribe')
                })
                .catch(err => {
                  console.log('Subscribe Error:', err)
                })
            } else {
              // that.newNotif('Subscribtion sudah ada')
            }
          })
        })
      },
      newNotif(msg) {
        // cek apakah service worker sudah teregister
        let opt = {
          body: msg, //adalah text dari notifikasi yang lebih jelas dari title
          icon: 'http://localhost:8080/api/icon.png', // bisa url ke luar atau local image
          // image: '../icon.png', // memunculkan image di notifikasi
          dir: 'ltr', // left to right (ltr) atau right to left (rtl)
          lang: 'en-US', //standard
          vibrate: [100, 50, 200], // device bergetar apabila ada notifikasi
          badge: 'http://localhost:8080/api/icon.png', // icon kecil di drawer
          // sound: '../notif.mp3', // sound saat notifikasi masuk (belum semua support)
          //! advance option
          // tag: 'confirm-notification', // kita men set tag agar notifikasi tidak menstack di device
          // renotify: true, // apabila tag sama di dalam beberapa notifikasi, maka status notifikasi akan dianggap baru dan device akan bergetar, jadi apabila false maka tag sama device tidak akan bergetar
          // actions: [
          //   //! list aksi saat notifikasi di pilih
          //   {
          //     action: 'confirm',
          //     title: 'Yes',
          //     icon: '../icon.png'
          //   },
          //   {
          //     action: 'cancel',
          //     title: 'No',
          //     icon: '../icon.png'
          //   }
          // ]
        }
        //! cara memunculkan notifikasi dengan windows object
        // new Notification('new notif', opt)
        //! cara menjalankan notifikasi dari service worker melalui navigator
        if ('serviceWorker' in navigator) {
          // apabila service worker ada , maka jalankan notifikasi melalui service worker
          navigator.serviceWorker.ready.then(function(sw) {
            // service worker menjalankan notifikasi
            sw.showNotification('Subscribe', opt)
          })
        }
      },
      urlB64ToUint8Array(base64String) {
        const padding = '='.repeat((4 - (base64String.length % 4)) % 4)
        const base64 = (base64String + padding)
          .replace(/\-/g, '+')
          .replace(/_/g, '/')
        const rawData = window.atob(base64)
        const outputArray = new Uint8Array(rawData.length)
        for (let i = 0; i < rawData.length; ++i) {
          outputArray[i] = rawData.charCodeAt(i)
        }
        return outputArray
      }
  },
  mounted() {
     this.subscribe()
      window.addEventListener("beforeinstallprompt", function(e) {
        e.preventDefault();
        deferredPrompt = e;
      });
  },
}
</script>
